
body {
  margin: 0;
}
.flex-1 {
  -ms-flex: 1;
      flex: 1;
}
.el-carousel__container {
  height: 100vh;
}
.el-carousel__item:nth-child(2n) {
  background-color: #4b515d;
}
.el-carousel__item:nth-child(2n+1) {
  background-color: #3a3c40;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.entry-enter-active,
.entry-leave-active {
  transition: all .5s;
}
.entry-enter,
.entry-leave-active {
  -ms-transform: translateX(500px);
      transform: translateX(500px);
}
.entry-page {
  background: #f0f3f3;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100vh;
  width: 100%;
}
.entry-page .img-cover {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  padding: 20px;
  background: rgba(0, 0, 0, 0.4);
}



