.login-box {
  width: 400px;
  position: absolute;
  top: 0;
  right: 100px;
  bottom: 0;
  z-index: 2;
  background: rgba(255, 255, 255, 0.3);
}
.login-box .password-desc {
  line-height: 1.2;
  margin-bottom: 11px;
  color: #fff;
}
.login-box .password-desc .el-icon-fa-lightbulb-o {
  color: #f7ba2a;
  font-size: 26px!important;
  margin-right: 5px;
}
.login-body__bottom {
  color: #e4e4e4;
  font-size: 14px;
  padding-bottom: 11px;
  margin-bottom: 11px;
  border-bottom: 1px solid #e7eaec;
}
.login-body__bottom span {
  color: #1ab394;
}
.login-footer {
  font-size: 14px;
  color: #fff;
  padding: 0 30px;
}



