.custom-fields .box-header__title {
  -ms-flex: 1;
      flex: 1;
  color: #999;
  font-size: 12px;
}
.custom-fields .box-header__title span {
  color: #f7ba2a;
}
.custom-fields .text-center {
  text-align: center;
}
.custom-fields .block-box {
  margin-bottom: 50px;
  background: #ffffff;
}
.custom-fields .block-box .block-box__title {
  height: 50px;
  line-height: 50px;
  padding-left: 20px;
  border-bottom: 1px #e6e6e6 solid;
}
.custom-fields .row-box {
  position: relative;
  height: 50px;
  line-height: 50px;
  padding-left: 20px;
  background: #ffffff;
}
.custom-fields .row-box:hover {
  background: #f9f9f9;
}
.custom-fields .row-box:hover .row-box__action {
  display: block;
}
.custom-fields .row-box .row-box__title {
  font-size: 14px;
}
.custom-fields .row-box .row-box__action {
  display: none;
  position: absolute;
  right: 10px;
  top: 0;
}
.custom-fields .row-box .row-box__action i {
  cursor: pointer;
  padding: 18px;
}
.custom-fields .el-collapse {
  margin-bottom: 30px;
}



