
.table-column-edit .cell {
  padding: 0;
}
.table-item {
  height: 100px;
  line-height: 100px;
  text-align: center;
}
.table-item--input {
  width: 100%;
  height: 100px;
  line-height: 100px;
  text-align: center;
}
.table-item--input .el-input__inner {
  height: 100px;
  text-align: center;
  border-radius: 0;
}
.table-item--input.is-disabled .el-input__inner {
  background-color: #fff;
  border-color: #fff;
  color: inherit;
  cursor: pointer;
}
.table-item--disabled {
  background-color: #e4e4e4;
}



